.container {
  width: em($container-width, $fz);
  padding-left: em($container-padding, $fz);
  padding-right: em($container-padding, $fz);
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  box-sizing: border-box;

  &--xl {
    width: em($container-width-xl, $fz);
  }

  &--sm {
    width: em($container-width-sm, $fz);
  }

  &--xs {
    width: em($container-width-xs, $fz);
  }
}
