.features {
  $self: &;

  margin-top: auto;
  padding: em(50 0, $fz_base);

  &__item {
    &:not(:first-child) {
      margin-top: em(53, $fz_base);;
    }

    &--list-wrap {
      border-top: 0.5px solid hsl(211, 23%, 45%);
      border-bottom: 0.5px solid hsl(211, 23%, 45%);
    }
  }

  &__title {
    font-weight: 500;
    margin-bottom: em(15, $fz_base);;

    #{$self}__label {
      $fz: 29;
      font-size: em($fz, $fz_base);
    }
  }

  &__descr {
    $fz: 19;
    font-size: em($fz, $fz_base);
    text-transform: uppercase;
  }
}

.features-list {
  $self: &;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: em(30 0, $fz_base);

  @include screen(md,max) {
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;

    margin-top: em(-30, $fz_base);
  }

  &__item {
    display: flex;
    align-items: center;

    @include screen(md,max) {
      margin-top: em(50, $fz_base);
    }
  }

  &__icon {
    position: relative;
    overflow: hidden;
    width: em(55, $fz_base);
    height: em(55, $fz_base);
    margin-right: em(30, $fz_base);

    &--shipping {
      width: em(57, $fz_base);
    }

    &--delivery {
      width: em(104, $fz_base);
    }

    &--choice {
      width: em(66, $fz_base);
      height: em(69, $fz_base);
    }

    #{$self}__image {
      @include fill-img;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
  }

  &__title {
    $fz: 29;
    font-size: em($fz, $fz_base);
  }

  &__label {
    $fz: 19;
    font-size: em($fz, $fz_base);
  }
}
