.footer {
  background-color: hsl(211, 23%, 33%);

  &__container {
    display: flex;
    padding-top: em(67, $fz);
    padding-bottom: em(49, $fz);

    @include screen(lg,max) {
      flex-wrap: wrap;
      padding-top: em(30, $fz);
      padding-bottom: em(30, $fz);
    }
  }

  &__contacts {
    @include screen(lg,min) {
      margin-left: auto;
    }

    @include screen(lg,max) {
      margin-top: em(50, $fz_base);
    }
  }
}

.footer-list {
  $self: &;

  text-transform: uppercase;
  color: hsl(0, 0%, 77%);

  @include screen(lg,min) {
    &:not(:first-child) {
      margin-left: em(183, $fz_base);
    }
  }

  @include screen(lg,max) {
    margin-top: em(50, $fz_base);
  }

  @include screen(lg,max) {
    width: 50%;
  }

  @include screen(sm,max) {
    width: 100%;

    &:first-child {
      margin-top: 0;
    }
  }

  &__item {

    #{$self}__label {
      $fz: 17;
      font-size: em($fz, $fz_base);
    }

    &:not(:first-child) {
      margin-top: em(8, $fz_base);
    }

    &--title {
      color: hsl(0, 0%, 100%);
      cursor: default;

      #{$self}__label {
        $fz: 22;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__link {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      height: 1px;
      background-color: hsl(0, 0%, 77%);
      transition: width $ease-main;
    }

    &:hover {
      &::before {
        width: 100%;
      }
    }
  }
}

.footer-contacts {
  display: flex;
  flex-direction: column;

  &__item {
    &:not(:first-child) {
      margin-top: em(26, $fz_base);
    }
  }
}

.footer-social {
  $self: &;

  display: flex;
  align-items: center;
  color: hsl(0, 0%, 100%);

  text-transform: uppercase;

  &__title {
    #{$self}__label {
      $fz: 22;
      font-size: em($fz, $fz_base);
    }
  }

  &__list {
    margin-left: em(14, $fz_base);
  }
}

.footer-social-list {
  display: flex;
  align-items: center;

  &__item {
    &:not(:first-child) {
      margin-left: em(17, $fz_base);
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;

    width: em(28, $fz_base);
    height: em(28, $fz_base);
    border-radius: 100px;
    background-color: hsl(211, 65%, 49%);
  }

  &__icon {
    width: em(15, $fz_base);
    height: em(15, $fz_base);
    fill: hsl(0, 0%, 100%);

    &--fb {
      width: em(9, $fz_base);
      height: em(18, $fz_base);
    }

    &--twitter {
      width: em(16, $fz_base);
      height: em(13, $fz_base);
    }

    &--instagram {
      width: em(16, $fz_base);
      height: em(16, $fz_base);
    }
  }
}

.footer-copyright {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: em(26 0, $fz_base);
  background-color: hsl(0, 0%, 26%);

  text-transform: uppercase;
  color: hsl(0, 0%, 77%);

  &__label {
    $fz: 17;
    font-size: em($fz, $fz_base);
  }
}
