.link {
  $self: &;

  display: flex;
  align-items: center;

  color: hsl(0, 0%, 100%);
  text-transform: uppercase;
  
  &__label {
    $fz: 35;
    font-size: em($fz, $fz_base);
    letter-spacing: 0.05em;

    + span {
      margin-left: em(20, $fz_base);
    }
  }

  &__arrow {
    position: relative;
    width: em(60, $fz_base);
    background-color: hsl(0, 0%, 100%);
    height: 2px;

    #{$self}--dark & {
      color: hsl(0, 0%, 26%);
      background-color: hsl(0, 0%, 26%);
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 1px;
      transform: translateY(-50%) rotate(45deg);

      width: em(18, $fz_base);
      height: em(18, $fz_base);

      border-top: 2px solid;
      border-right: 2px solid;
    }
  }
}
