.navigation {
  display: flex;

  @include screen(lg,max) {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;

    flex-direction: column;
    background-color: $color-bg;

    width: 75%;
    padding: em(65 200 100 100, $fz_base);

    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
    z-index: 16;

    transform: translateX(120%);
    transition: transform $ease-main;

    &.is-active {
      transform: translateX(0);
    }
  }

  @include screen(md,max) {
    padding: em(35 50 50, $fz_base);
  }

  @include screen(xs,max) {
    width: 100%;
  }

  &__list {
    &:not(:first-child) {
      margin-left: em(211, $fz_base);
    }

    @include screen(xl, max) {
      &:not(:first-child) {
        margin-left: em(100, $fz_base);
      }
    }

    @include screen(lg, max) {
      &:not(:first-child) {
        margin-left: 0;
      }

      &:not(:first-child) {
        margin-top: em(100, $fz_base);
      }
    }
  }
}

.links-list {
  display: flex;
  align-items: center;

  text-transform: uppercase;
  color: hsl(0, 0%, 100%);

  @include screen(lg,max) {
    flex-direction: column;
    align-items: flex-start;
    color: hsl(211, 65%, 49%);
  }

  &__item {
    @include screen(lg,min) {
      &:not(:first-child) {
        margin-left: em(51, $fz_base);
      }
    }

    @include screen(lg,max) {
      &:not(:first-child) {
        margin-top: em(51, $fz_base);
      }
    }
  }

  &__link {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      height: 1px;
      background-color: hsl(0, 0%, 100%);
      transition: width $ease-main;
    }

    &:hover {
      &::before {
        width: 100%;
      }
    }
  }

  &__label {
    $fz: 17;
    font-size: em($fz, $fz_base);
  }
}
