.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 15;

  width: 100%;

  padding-top: em(65, $fz_base);
  padding-bottom: em(65, $fz_base);
  background-color: transparent;

  @include screen(md,max) {
    padding-top: em(35, $fz_base);
    padding-bottom: em(35, $fz_base);
  }

  &__container {
    display: flex;
    align-items: center;
  }

  &__buttons {
    margin-left: auto;
  }

  &__burger {
    z-index: 6;
  }
}

.logo {
  width: em(109, $fz_base);
  text-transform: uppercase;
  color: hsl(0, 0%, 100%);

  @include screen(lg,min) {
    margin-right: em(177, $fz_base);
  }

  &__label {
    $fz: 24;
    font-size: em($fz, $fz_base);
  }
}

.header-buttons {
  fill: hsl(0, 0%, 100%);

  &__button {
    display: inline-block;
    width: em(32, $fz_base);
    height: em(32, $fz_base);

    &:not(:first-child) {
      margin-left: em(48, $fz_base);
    }

    @include screen(xs,max) {
      &:not(:first-child) {
        margin-left: em(20, $fz_base);
      }
    }

    &--log-out {
      width: em(42, $fz_base);
    }
  }

  &__icon {
    width: 100%;
    height: 100%;
  }
}

.burger {
  $self: &;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: em(48, $fz_base);

  position: relative;
  z-index: 17;

  @include screen(lg,min) {
    display: none;
  }

  @include screen(xs,max) {
    margin-left: em(20, $fz_base);
  }

  &__line {
    position: relative;
    display: block;

    width: em(45, $fz_base);
    height: em(12, $fz_base);

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: hsl(0, 0%, 100%);
      transition: transform $ease-main, color $ease-main;
    }

    &::before {
      top: 0;

      #{$self}.is-active & {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
        background-color: hsl(211, 65%, 49%);
      }
    }

    &::after {
      bottom: 0;

      #{$self}.is-active & {
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
        background-color: hsl(211, 65%, 49%);
      }
    }
  }
}
