.banner {
  $self: &;

  position: relative;
  overflow: hidden;
  min-height: em(632, $fz_base);

  display: flex;
  align-items: center;
  justify-content: center;

  @include screen(lg, max) {
    min-height: em(1200, $fz_base);
  }

  @include screen(md, max) {
    font-size: 1.5vw;
  }

  &__bg {
    #{$self}__image {
      @include fill-img;
      top: em(-316, $fz_base);
    }
  }

  &__card {
    position: relative;
    z-index: 1;
  }
}

.banner-card {
  display: flex;
  align-items: center;
  justify-content: center;

  width: em(862, $fz_base);
  height: em(262, $fz_base);
  background-color: hsla(0, 0%, 100%, 0.8);

  text-transform: uppercase;

  &__msg {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__label {
    $fz: 75;
    font-size: em($fz, $fz_base);
    color: hsl(211, 23%, 33%);

    &--accent {
      color: hsl(0, 78%, 64%);
    }

    &--extra {
      $fz: 100;
      font-size: em($fz, $fz_base);
    }
  }
}
