.intro {
  position: relative;

  &__container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;

    .swiper-button {
      position: static;
      width: em(30, $fz_base);
      height: em(30, $fz_base);

      border: 2px solid transparent;
      border-radius: 4px;

      @include screen(lg,max) {
        display: none;
      }

      &::after {
        content: '';
      }

      &:not(:first-child) {
        margin-top: em(30, $fz_base);
      }

      &-prev {
        border-bottom-color: hsl(0, 0%, 100%);
        border-left-color: hsl(0, 0%, 100%);
        transform: rotate(45deg);
      }

      &-next {
        border-top-color: hsl(0, 0%, 100%);
        border-right-color: hsl(0, 0%, 100%);
        transform: rotate(45deg);
      }
    }
  }

  &__wrap {
    position: absolute;
    bottom: em(90, $fz_base);
    right: 0;
    width: 100%;
    z-index:  4;

    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    padding-left: em($container-padding, $fz_base);

    @include screen(lg,min) {
      padding-left: em(301, $fz_base);
    }

    @include screen(sm,max) {
      flex-direction: column;
    }
  }

  &__button {
    @include screen(sm,max) {
      right: auto !important;
      margin-right: 0 !important;
      margin-top: em(30, $fz_base);
    }
  }
}

.intro-screen {
  $self: &;

  display: flex;
  align-items: center;
  justify-content: center;
  min-height: em(1100, $fz_base);

  @include screen(md,max) {
    align-items: flex-start;
    padding-top: em(150, $fz_base);
    min-height: em(800, $fz_base);
  }

  &__container {
    position: relative;
    z-index: 2;

    @include screen(lg,min) {
      padding-left: calc(#{em(320 + $container-padding, $fz_base)});
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include screen(md,max) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(#000, 0.5);
      z-index: 1;
    }
  }

  &__image {
    @include fill-img;
  }

  &__title {
    display: flex;
    flex-direction: column;
    color: #fff;

    #{$self}__label {
      $fz: 145;
      font-size: em($fz, $fz_base);

      @include screen(md,max) {
        $fz: 80;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__promotion {
    text-transform: uppercase;
    border: 2px solid hsl(0, 0%, 100%);
    color: hsl(0, 0%, 100%);
    padding: em(15 25, $fz_base);
    max-width: em(282, $fz_base);

    @include screen(md,max) {
      margin-top: em(30, $fz_base);
    }

    #{$self}__label {
      $fz: 35;
      font-size: em($fz, $fz_base);
    }
  }
}

.intro-next-button {
  position: relative;
  display: flex;
  flex-direction: column;

  &__wrap {
    display: flex;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: em(113, $fz_base);
    height: em(184, $fz_base);
    background-color: #fff;
  }

  &__pagination {
    position: relative;
    bottom: em(-10, $fz_base) !important;
    width: 100%;
    display: flex;
    z-index: 3;

    span {
      background-color: #fff;
      margin: 0 !important;
      height: 2px;
      flex: 1;
      border-radius: 0;
    }
  }
}

.intro-next-button-slider {
  width: em(341, $fz_base);
  height: em(183, $fz_base);
  z-index: 3;

  &__slide {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }

  &__image {
    @include fill-img;
  }
}
