.products {
  $self: &;

  padding: em(50 0 70, $fz_base);

  &__title {
    margin-bottom: em(31, $fz_base);

    color: hsl(0, 0%, 26%);
    text-transform: uppercase;
    font-weight: 500;

    text-align: center;

    #{$self}__label {
      $fz: 48;
      font-size: em($fz, $fz_base);
    }
  }

  &__banners {
    margin-top: em(44, $fz_base);
  }
}

.side-bar {

  @include screen(lg,max) {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100vh;
    max-width: em(270, $fz_base);

    padding: em(15 12 30, $fz_base);
    background-color: $side-bar-bg;

    transform: translateX(-120%);
    transition: transform $ease-main;

    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10;

    &.is-active {
      transform: translate(0);
    }
  }

  &__filter {
    @include screen(lg,min) {
      padding: em(15 12 30, $fz_base);
      background-color: $side-bar-bg;
    }
  }

  &__close {
    position: absolute;
    top: em(12, $fz_base);
    right: em(12, $fz_base);

    width: em(30, $fz_base);
    height: em(30, $fz_base);

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: hsl(211, 65%, 49%);
    }

    &::before {
      transform: translateY(-50%) rotate(45deg);
    }

    &::after {
      transform: translateY(-50%) rotate(-45deg);
    }

    @include screen(lg, min) {
      display: none;
    }
  }
}

.side-bar-list {
  $self: &;

  padding: em(15 0, $fz_base);
  border-bottom: 1px solid hsl(0, 0%, 77%);


  &__title {
    color: hsl(211, 65%, 49%);
    text-transform: uppercase;
    margin-bottom: em(15, $fz_base);

    #{$self}__label {
      $fz: 21;
      font-size: em($fz, $fz_base);
    }
  }

  &__item {
    display: flex;
    align-items: center;

    &:not(:first-child) {
      margin-top: em(10, $fz_base);
    }

    #{$self}__label {
      $fz: 17;
      font-size: em($fz, $fz_base);
    }
  }

  &__stars {
    display: flex;
    align-items: center;
    margin-right: em(8, $fz_base);

    &--half {
      #{$self}__star {
          &:last-child {
            position: relative;

          &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            width: 50%;
            z-index: 2;
            background-color: $side-bar-bg;
          }
        }
      }
    }
  }

  &__star {
    fill: hsl(46, 100%, 50%);
    width: em(20, $fz_base);
    height: em(20, $fz_base);
  }

  &__icon {
    fill: inherit;
    width: 100%;
    height: 100%;
  }

  &__wrap {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__checkbox {
    width: em(17, $fz_base);
    height: em(17, $fz_base);
    background-color: hsl(0, 0%, 100%);
    border: 0.5px solid #C4C4C4;
    margin-right: em(9, $fz_base);
  }
}

.products-banners {
  &__banner {
    &:not(:first-child) {
      margin-top: em(56, $fz_base);
    }
  }
}

.products-banners-banner {
  $self: &;

  display: flex;
  flex-direction: column;

  position: relative;
  padding: em(15 18 13, $fz_base);
  overflow: hidden;
  width: 100%;
  height: em(414, $fz_base);
  text-transform: uppercase;

  &::before {
    content: '';
    position: absolute;
    border-radius: 100%;
  }

  &__title {
    #{$self}__label {
      $fz: 18;
      font-size: em($fz, $fz_base);
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;

    position: relative;
    z-index: 2;

    #{$self}__label {
      $fz: 27;
      font-size: em($fz, $fz_base);
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
  }

  &--wallbox {
    color:hsl(0, 0%, 100%);
    background-color: hsl(211, 65%, 49%);

    &::before {
      bottom: em(-145, $fz_base);
      left: em(22, $fz_base);

      width: em(326, $fz_base);
      height: em(326, $fz_base);
      background-color: hsl(212, 43%, 82%);
    }

    #{$self}__descr {
      margin-top: em(27, $fz_base);

      #{$self}__label {
        $fz: 27;
        font-size: em($fz, $fz_base);

        &:first-child {
          max-width: em(173, $fz);
        }

        &--accent {
          $fz: 21;
          font-size: em($fz, $fz_base);
          color: hsl(46, 100%, 50%);
          font-weight: 400;
          margin-top: em(13, $fz);
        }
      }
    }

    #{$self}__image {
      width: em(188, $fz_base);
      height: em(245, $fz_base);
    }
  }

  &--post {
    color:hsl(211, 65%, 49%);
    background-color: hsl(330, 6%, 93%);
    padding-bottom: em(50, $fz_base);

    &::before {
      bottom: em(-57, $fz_base);
      left: em(56, $fz_base);

      width: em(361, $fz_base);
      height: em(361, $fz_base);
      background-color: hsl(212, 43%, 82%);
    }

    #{$self}__descr {
      margin-top: auto;

      #{$self}__label {
        $fz: 27;
        font-size: em($fz, $fz_base);
        line-height: 1;

        &:first-child {
          margin-bottom: em(8, $fz_base);
        }

        &--accent {
          $fz: 29;
          font-size: em($fz, $fz_base);
          color: hsl(0, 0%, 0%);
          font-weight: 500;

          display: flex;
          flex-direction: column;
        }

        &--thin {
          $fz: 21;
          font-size: em($fz, $fz_base);
        }
      }
    }

    #{$self}__image {
      width: em(184, $fz_base);
      height: em(335, $fz_base);
    }
  }
}

.filter-button {
  width: em(50, $fz_base);
  height: em(50, $fz_base);

  background-color: hsla(0,0%,100%,.7);
  fill: hsl(0, 0%, 26%);

  display: flex;
  align-items: center;
  justify-content: center;

  &__icon {
    width: em(30, $fz_base);
    height: em(30, $fz_base);
  }
}

.products-place {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  &__item {
    &:not(:first-child) {
      margin-top: em(26, $fz_base);
    }
  }

  &__filter {
    @include screen(lg,min) {
      display: none;
    }

     position: absolute;
     top: 0;
     left: 0;
     transform: translateY(calc(-100% - #{em(15, $fz_base)}));
  }
}

.products-card {
  $self: &;

  padding: em(30 40, $fz_base);
  background-color:  hsla(0, 0%, 100%, 0.7);

  @include screen(xs,max) {
    font-size: 3.5vw;
  }

  &__header {
    color:hsl(0, 0%, 100%);
    text-transform: uppercase;
    text-align: center;

    margin-bottom: em(42, $fz_base);
    padding: em(20 10, $fz_base);

    @include ellipsis(100%);

    #{$self}__label {
      $fz: 40;
      font-size: em($fz, $fz_base);
    }

    &--latest {
      background-color: hsl(0, 78%, 64%);
    }

    &--best {
      background-color: hsl(46, 100%, 50%);
    }

    &--fast {
      background-color: hsl(211, 23%, 45%);
    }
  }
}

.products-card-slots {
  display: flex;
  justify-content: space-between;

  @include screen(md,max) {
    flex-wrap: wrap;
    justify-content: space-around;

    &__item {
      margin-top: em(30, $fz_base);
    }
  }
}

.products-card-slot {
  background-color: hsl(0, 0%, 100%);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);

  padding: em(30 20, $fz_base);
  text-align: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: em(317, $fz_base);
  min-height: em(432, $fz_base);

  text-transform: uppercase;

  @include screen(lg,max) {
    @include screen(md,min) {
      width: em(300, $fz_base);
    }
  }

  &__title {
    margin-bottom: em(10, $fz_base);
  }

  &__picture {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__image {
    max-width: 100%;
    max-height: em(200, $fz_base);
  }

  &__label {
    display: block;
    $fz: 27;
    font-size: em($fz, $fz_base);

    &--thin {
      $fz: 21;
      font-size: em($fz, $fz_base);
    }
  }

  &__name {
    margin-top: auto;
  }

  &__add {
    display: block;
    width: em(185, $fz_base);
    text-align: center;

    margin: auto auto 0;
    padding: em(10, $fz_base);
    background-color: hsl(211, 65%, 49%);

    color:hsl(0, 0%, 100%);
    text-transform: uppercase;
  }
}
