.form-action {
  $self: &;

  display: flex;
  width: 100%;
  
  &__input {
    flex-grow: 1;
    padding: em(10 8, $fz_base);
    background-color: hsl(0, 0%, 100%);
    text-transform: uppercase;
    color: hsl(0, 0%, 77%);

    &::placeholder {
      color: hsl(0, 0%, 77%);
    }
  }

  &__button {
    width: em(44, $fz_base);
    background-color: hsl(211, 65%, 49%);
    color: hsl(0, 0%, 100%);
    text-transform: uppercase;
    margin-left: em(5, $fz_base);

    #{$self}__label {
      $fz: 17;
      font-size: em($fz, $fz_base);
    }
  }
}