// Здесь используем комментарии только через "//", т.к. они нам не нужны в итоговом css-файле
$fz_base: 16;
$fz: $fz_base;
// ##########
// Базовые настройки сетки сайта
// ##########
$container-width-xl: 1825; // Максимальная ширина контейнера контента
$container-width: 1520; // Максимальная ширина контейнера контента
$container-width-sm: 1382; // Максимальная ширина контейнера контента
$container-width-xs: 1280; // Максимальная ширина контейнера контента
$container-padding: 15; // Отступы слева и справа у контейнера
$grid-container-width: 100%; // Ширина контейнера сетки
$grid-cols: 10; // Количество колонок в сетке
$grid-gap: 35; // Расстояние между колонками
$grid-gap-vertical: 35; // Расстояние между строками в сетке
$breakpoints: (
  'xl': 1400px,
  'lg': 1280px,
  'md': 1024px,
  'sm': 768px,
  'xs': 480px,
);

// ##########
// Цвета
// ##########
$color-basic: hsl(0, 0%, 0%); // Основной цвет текста (применяется к body)
$color-placeholder: hsl(0, 0%, 47%); // Основной цвет для placeholder
$color-input: hsl(0, 0%, 0%); // Основной цвет input, select и textarea
$color-main: hsl(224, 73%, 49%); // Основной фирменный цвет
$color-border: hsl(0, 0%, 80%); // Основной цвет границ таблиц, секций
$color-bg: hsl(26, 11%, 88%);
$side-bar-bg: hsl(30, 13%, 94%);

// ##########
// Transitions
// ##########
$ease-main: 0.2s ease;

// ##########
// Шрифты
// ##########
$font-main: 'Cervo', Arial, sans-serif; // Основной шрифт сайта
